'use client'
import {
  Ordersoverview,
  Stockalerts,
  Paymentgateways,
  Profitlosssummary,
  Marketingcampaigns,
  Trafficsources,
  Customerfeedback,
  Usermanagement,
  Notifications,
  Realtimeinsights,
} from "@/components/content";

import { useState } from "react";
import DashboardHeader from "@/components/ui/DashboardHeader";
import Weeklyperformance from "../content/weeklyperformance";

const components = [
  { id: 1, component: <Keymetrics /> },
  { id: 2, component: <Realtimeinsights /> },
  { id: 3, component: <Revenueupdate /> },
  { id: 4, component: <Saleoverview /> },
  { id: 5, component: <Ordersoverview /> },
  { id: 6, component: <Weeklyperformance /> },
  { id: 7, component: <Profitlosssummary /> },
  { id: 8, component: <Marketingcampaigns /> },
  { id: 9, component: <Trafficsources /> },
  { id: 10, component: <Stockalerts /> },
  { id: 11, component: <Customerfeedback /> },
  { id: 12, component: <Usermanagement /> },
  { id: 13, component: <Paymentgateways /> },
  { id: 14, component: <Notifications /> },
];

const Pagination = ({ totalPages, currentPage, onPageChange }) => (
  <div className="flex justify-center mt-6 space-x-2">
    {Array.from({ length: totalPages }, (_, index) => (
      <button
        key={index}
        onClick={() => onPageChange(index + 1)}
        className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
          currentPage === index + 1
            ? "bg-blue-500 text-white shadow-lg"
            : "bg-gray-200 text-gray-700 hover:bg-blue-100"
        }`}
      >
        {index + 1}
      </button>
    ))}
  </div>
);

export default function MainContent() {
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 4;

  const totalPages = Math.ceil(components.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const currentComponents = components.slice(
    startIndex,
    startIndex + itemsPerPage
  );

  return (
    <main className="flex-1 p-6 bg-gray-50 overflow-y-auto">
      {/* Dashboard Header */}
      <DashboardHeader />

      {/* Content Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-2 gap-6 mt-6">
        {currentComponents.map((item) => (
          <div
            key={item.id}
            className="bg-white shadow-md rounded-lg p-4 transition-all hover:shadow-lg"
          >
            {item.component}
          </div>
        ))}
      </div>

      {/* Pagination */}
      <Pagination
        totalPages={totalPages}
        currentPage={currentPage}
        onPageChange={setCurrentPage}
      />
    </main>
  );
}
